.a{
    height: 100vh;
    /* background-color: aqua; */
    display: flex;
}

.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;

}
.a-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    
    
}
.a-card.bg{
    position: absolute;
    top: 40px;
    left: 50px;
    background-color: #333;
}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}
.a-sub{
    margin: 20px,0px;
}
.a-desc{
    font-weight: 400;
}
.a-image{
    width: 100%;
    height: 135%;
    object-fit: cover;
}



@media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}
