.p{
    width: 30%;
    height: 40vh;
   
    margin: 20px 10px;
    border: 2px solid rgb(243,242,242);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}
.p-browzer{
    height: 20px;
    background-color: rgb(243,242,242);
    display: flex;
    align-items: center;
}
.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: #fff;
}
.p-image{
    width: 100%;
    /* transition: all 10s ease; */
    transition: all .2s ease-in-out;
}
.p-image:hover{
    transform: scale(1.2);
}
@media screen and (max-width: 480px) {
    .p {
      width: 40%;
      height: 20vh;
    }
  }